* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Arial;
}

.people {
  grid-template-columns: repeat(2, 220px);
  justify-content: center;
  gap: 40px 10px;
  padding: 30px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 220px);
  }
}

.person {
  width: 220px;
  height: 250px;
  padding: 10px;
  overflow: hidden;
}

.person .person__photo {
  width: 200px;
  position: relative;
}

.person__img {
  border-radius: 50%;
  width: 200px;
  height: 200px;
}

.person .person__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-family: Arial;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 84px;
  right: 10px;
}

.person__details {
  margin-top: 8px;
}

.person__name {
  color: #2f4f4f;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: 700;
  display: flex;
}

.person__extra {
  text-align: center;
  margin-top: 9px;
  font-size: 18px;
  font-weight: 400;
}

.person:hover {
  background-color: #eee;
  overflow: visible;
}
